import React, { useState, useEffect, useRef } from 'react';
import { tvDummy } from '../tvDummy'
import MediaCard from '../components/MediaCard';
import { useTvContext } from '../contexts/TvContext';
import { LoaderOverlay, Spinner, ScrollDetector } from '../AppContent'


export default function Tv() {
  const ITEMS_PER_LOAD = 16; // 처음에 16개, 이후 8개씩 추가
    const [displayCount, setDisplayCount] = useState(ITEMS_PER_LOAD);
    const [isLoading, setIsLoading] = useState(false);
    const scrollDetectorRef = useRef(null); // 스크롤 감지용 ref

    const { sortedTvShows } = useTvContext();
  
    useEffect(() => {
      // 모든 영화가 이미 로드되었다면 더 이상 Observer를 등록할 필요 없음
      if (displayCount >= tvDummy.results.length) return;
  
      const observer = new IntersectionObserver(
        (entries) => {
          const target = entries[0];
          // 스크롤 감지 요소가 뷰포트에 진입했고, 로딩 중이 아니며,
          // 아직 모든 영화를 보여주지 않았을 때만 로딩 시작
          if (target.isIntersecting && !isLoading && displayCount < tvDummy.results.length) {
            setIsLoading(true); // 로딩 시작
  
            setTimeout(() => {
              setDisplayCount((prevCount) => {
                const currentDisplayed = prevCount;
                const nextBatchSize = ITEMS_PER_LOAD;
                return Math.min(currentDisplayed + nextBatchSize, tvDummy.results.length);
              });
              setIsLoading(false); // 로딩 완료
            }, 700); // 지연 (테스트 목적)
          }
        },
        {
          threshold: 1.0, // 스크롤 감지 요소가 뷰포트의 80% 정도 진입했을 때 감지
          rootMargin: '100px 0px 100px 0px' // 하단에 도달하기 전에 미리 감지하도록 여유 설정
        }
      );
  
      // scrollDetectorRef가 가리키는 요소가 DOM에 있을 때만 Observer를 연결
      if (scrollDetectorRef.current) {
        observer.observe(scrollDetectorRef.current);
      }
  
    }, [displayCount, isLoading]); // displayCount 또는 isLoading이 변경되면 useEffect 재실행
  
    return (
      <>
        <>
          {sortedTvShows.slice(0, displayCount).map((item) => (
            <MediaCard data={item} mediaType="tv" key={item.id} />
          ))}
        </>
        
        {/* ScrollDetector: 이 요소가 뷰포트에 들어오면 추가 로드를 시작합니다. */}
        {/* 모든 영화가 로드되지 않았을 때만 Detector를 렌더링합니다. */}
        {displayCount < tvDummy.results.length ? (
          <ScrollDetector ref={scrollDetectorRef} />
        ) : (
          // 모든 영화를 로드했을 때 표시할 메시지
          tvDummy.results.length > 0
        )}
  
        {/* LoaderOverlay: 로딩 중일 때만 전체 화면을 덮는 오버레이와 스피너를 보여줍니다. */}
        {isLoading && (
          <LoaderOverlay>
            <Spinner />
          </LoaderOverlay>
        )}
      </>
    );
}
