import axios from 'axios';

// 환경 변수 설정 (.env 파일에 저장)

// API 호출 함수를 export 합니다.
export async function getArtistInfo(artistName) {
  try {
    const response = await axios.get('https://api.discogs.com/database/search', {
      params: {
        q: artistName,
        type: 'artist',
        per_page: 10 // 검색 결과를 10개로 제한
      },
      headers: {
        'User-Agent': 'REACT_API_TEST/1.0',
        'Authorization': `Discogs key=${process.env.REACT_APP_DISCOGS_USER_TOKEN}, secret=${process.env.REACT_APP_DISCOGS_USER_SECRET}`
      }
    });
    
    return response.data;
  } catch (error) {
    console.error('Discogs API 아티스트 검색 에러:', error);
    if (error.response) {
      console.error('응답 데이터:', error.response.data);
      console.error('응답 상태:', error.response.status);
    } else if (error.request) {
      console.error('요청은 전송됐으나 응답 없음:', error.request);
    } else {
      console.error('요청 설정 중 오류:', error.message);
    }
    throw error;
  }
}

export async function getArtistReleases(artistId) {
  try {
    const response = await axios.get(`https://api.discogs.com/artists/${artistId}/releases`, {
      headers: {
        'Authorization': `Discogs key=${process.env.REACT_APP_DISCOGS_USER_TOKEN}, secret=${process.env.REACT_APP_DISCOGS_USER_SECRET}`
      },
      params: {
        per_page: 50, // 한 아티스트의 릴리즈는 최대 50개까지 가져옵니다. (필요에 따라 늘릴 수 있습니다)
        sort: 'year', // 연도별 정렬
        sort_order: 'desc' // 최신 앨범부터
      }
    });

    return response.data;
  } catch (error) {
    console.error(`Discogs API 아티스트 릴리즈(앨범) 에러 for ID ${artistId}:`, error);
    if (error.response) {
      console.error('응답 데이터:', error.response.data);
      console.error('응답 상태:', error.response.status);
    } else if (error.request) {
      console.error('요청은 전송됐으나 응답 없음:', error.request);
    } else {
      console.error('요청 설정 중 오류:', error.message);
    }
    throw error;
  }
}

// getReleaseDetails 함수에 에러 처리 강화
export async function getReleaseDetails(releaseId) {
  try {
    const response = await axios.get(`https://api.discogs.com/releases/${releaseId}`, {
      headers: {
        'Authorization': `Discogs key=${process.env.REACT_APP_DISCOGS_USER_TOKEN}, secret=${process.env.REACT_APP_DISCOGS_USER_SECRET}`
      }
    });
    
    return response.data;
  } catch (error) {
    console.error(`Discogs API 릴리즈 상세 정보 에러 for ID ${releaseId}:`, error);
    
    // 404 에러일 경우 기본 트랙리스트 반환
    if (error.response && error.response.status === 404) {
      return { 
        tracklist: [], 
        title: "정보 없음",
        year: "-",
        artists_sort: "-"
      };
    }
    
    throw error;
  }
}